.group,
.item {
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  border-bottom: 1px solid var(--border-1);
  display: flex;
  user-select: none;
  -webkit-user-drag: none;
  align-items: center;
}

.group:first-child {
  border-top: 1px solid var(--border-1);
}

.group {
  justify-content: flex-start;
  color: var(--text-3);
  font-weight: 500;
  font-size: 12px;
}

.item {
  justify-content: flex-end;
  cursor: pointer;

  &:hover,
  &.active {
    background: linear-gradient(to left, var(--primary-1) 0%, var(--bg-1) 100%);
    font-weight: 500;
  }

  &.active {
    text-decoration: underline;
    font-weight: 500;
  }

  &:active {
    background: linear-gradient(to left, var(--primary-2) 0%, var(--bg-1) 100%);
  }
}
