.wrapper {
  height: 40px;
  padding: 20px;
  display: flex;

  img {
    height: 100%;
    margin-right: 15px;
  }

  .title {
    font-size: 16px;
    font-weight: bold;
    line-height: 20px;
    font-family: Cinzel;
    font-weight: 900;
  }

  .desc {
    font-size: 13px;
    line-height: 22px;
    color: var(--text-2);
    font-weight: 700;
    text-decoration: underline;
  }
}
