
.wrapper {
  position: relative;
  border-right: 3px solid var(--primary-1);
}

.top {
  background-color: var(--bg-1);
  color: var(--text-1);
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 60px;
}

.bottom {
  height: 60px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--text-1);
  border-top: 1px solid var(--border-1);
}

.button {
  height: 34px;
  box-sizing: border-box;
  padding: 8px 16px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 800;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  background-color: var(--bg-1);
  color: var(--text-1);
  border: 1px solid var(--border-1);

  &:first-child {
    margin-right: 10px;
  }

  &:hover {
    background-color: var(--primary-1);
  }

  &:active {
    background-color: var(--primary-2);
  }
}